<template>
  <div>
    <div class="container px-4 mx-auto">
      <div class="flex space-x-10 items-start">
        <div v-for="(item, index) in myProject" :key="index" class="rounded bg-gray-200 w-full md:w-1/4 p-3">
          <div class="flex justify-between py-1">
            <span class="text-sm font-bold">{{item.title}}</span>
            <svg class="h-4 fill-current text-gray-dark cursor-pointer" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M5 10a1.999 1.999 0 1 0 0 4 1.999 1.999 0 1 0 0-4zm7 0a1.999 1.999 0 1 0 0 4 1.999 1.999 0 1 0 0-4zm7 0a1.999 1.999 0 1 0 0 4 1.999 1.999 0 1 0 0-4z"/></svg>
          </div>
          <div class="text-sm mt-2 -mb-2">
            <template v-for="(pbodyItem, pbodtIndex) in showProjectBody" :key="pbodtIndex">
              <div v-if="item[pbodyItem]" :class="subCardContainerCSS">
                <p :class="subCardTitleCSS">{{$t('project.' + pbodyItem)}}</p>
                <p :class="subCardBodyCSS">
                  {{item[pbodyItem]}}
                </p>
              </div>
            </template>
            <div v-if="item.image" :class="subCardContainerCSS">
              <p :class="subCardTitleCSS">{{$t('project.snapshot')}}</p>
              <p :class="subCardBodyCSS">
                <img :src="webImg(item.image)" />
              </p>
            </div>
            <div class='text-center m-1'>
              <a v-if="item.url" :href="item.url" target="_blank" :class="'mt-3 text-' + defaultColor + '-500'">{{$t('project.gotourl')}}</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
