<script>
import config from '@/config'
export default {
  name: "ProjectView",
  data () {
    return {
      config
    }
  },
  computed: {
    defaultColor () {
      return this.config.colorName
    },
    subCardContainerCSS () {
      return 'bg-white p-2 rounded my-2 border-b border-gray cursor-pointer hover:bg-gray-lighter'
    },
    subCardTitleCSS () {
      return 'font-bold mb-1'
    },
    subCardBodyCSS () {
      return 'm-2'
    },
    myProject () {
      return this.config.project
    },
    showProjectBody () {
      return this.config.showProjectBody
    }
  },
  methods: {
    webImg (image) {
      return 'image/web-snapshot/' + image
    }
  }
};
</script>
